import React, {Component} from "react";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";

import {fetchMiniCart, removeVoucher} from "eshop/modules/cart/actions/cart";
import {getCartEntries, getCartIsNet, getMiniCart, getPriceIsNet, getSelectedVoipNumber} from "../../selectors";
import MobileComponent from "./mobile/MobileComponent";
import FixComponent from "./fix/FixComponent";
import SimfreeComponent from "./simfree/SimfreeComponent";
import ConvergentComponent from "./convergent/ConvergentComponent";
import TvUpsellComponent from "./tvUpsell/TvUpsellComponent";

import BundleTypeEnum from "./BundleTypeEnum";
import {subscribeCustomerSelected} from "../../../checkout/actions/data";
import MsisdnSimCardPickerModal from "../MsisdnSimCardPickerModal";
import LowerInstallmentsModal from "../LowerInstallmentsModal";
import CartErrorModal from "./CartErrorModal";
import TooltipFromHtml from "eshop/modules/core/components/ui/TooltipFromHtml";
import TransferComponent from "./fix/TransferComponent";
import OraVasPacketPickerComponent from "eshop/modules/simfree/components/modals/OraVasPacketPickerComponent";

class MulticartEntriesListContainer extends Component {

    constructor(props){
        super(props)
        this.state={
            assignmentBoxCheck: false
        };
        this.checkAssignmentDeathBox = this.checkAssignmentDeathBox.bind(this);
        this.removeVoucher = this.removeVoucher.bind(this);
    }

    checkAssignmentDeathBox(){
        this.setState({
            assignmentBoxCheck: !this.state.assignmentBoxCheck
        })
    }

    componentDidMount() {
        this.props.fetchMiniCart();
        this.props.subscribeCustomerSelected();
    }

    lowerInstallmentsActive() {
        return !!this.props.isLowerInstallmentsVisible;
    }

    removeVoucher(event, voucher) {
        event.preventDefault();
        this.props.removeVoucher(voucher);
    }

    render() {
        let lowerInstallmentsActive = this.lowerInstallmentsActive();
        return (
            <div>
                {this.props.entries && this.props.entries.length > 0 &&
                <div className="g-gray1-bg u-padding-top-l ">
                    {this.props.miniCart.vouchers && this.props.miniCart.vouchers.map((voucher, idx) =>
                        <div key={idx} className={`l-full-row ${idx > 0 ? 'u-margin-top-l' : ''}`}>
                            <div className="l-page-row">
                                <div className="opl-msg opl-msg--box opl-msg--info u-padding-top u-padding" key="disclaimer">
                                    <div className="u-inline-block">{TooltipFromHtml.conditionalRender(voucher.name)}</div>
                                    <a href="#" onClick={(event) => this.removeVoucher(event, voucher)} className="u-right g-black1-c">Usu≈Ñ</a>
                                </div>
                            </div>
                        </div>)
                    }
                    {
                        this.props.entries.map((entry, idx) => {
                            const bundleType = entry.bundleType;
                            const sectionConf = this.props.entryTitle[bundleType.toString()];
                            if (bundleType === BundleTypeEnum.HARDBUNDLE && entry.entries.length === 1) {
                                sectionConf.sectionTitle = this.props.entryTitle[entry.entries[0].bundleType.toString()].sectionTitle;
                            }

                                const bundleProps = {
                                    descriptions: this.props.descriptions,
                                    sectionConf,
                                    entry,
                                    idx,
                                    channel: this.props.channels.sales,
                                    sapReservation: this.props.reservationNumberSapPOS,
                                    sapErrorMessage: this.props.descriptions.sapReservationError,
                                    lowerInstallmentsActive: lowerInstallmentsActive && !(entry && entry.euroSets && entry.euroSets.length > 0 &&
                                        !this.props.isLowerInstallmentsForEuroSetsVisible) &&
                                        !(entry.terminals && entry.terminals[0] &&
                                            !entry.terminals[0].priceOffset &&
                                            entry.terminals[0].monthlyPrices &&
                                            entry.terminals[0].monthlyPrices.length===1 &&
                                            entry.terminals[0].monthlyPrices[0].price===0),
                                    showNetPrices: this.props.showNetPrices,
                                    checkAssignmentDeathBox: this.checkAssignmentDeathBox,
                                    assignmentDeathCheckBoxState: this.state.assignmentBoxCheck
                                };

                            switch (bundleType) {
                            case BundleTypeEnum.VOICE:
                                return <MobileComponent key={"VOICE_"+idx} {...bundleProps} />;
                            case BundleTypeEnum.DATA:
                                return <MobileComponent key={"DATA_"+idx} {...bundleProps} />;
                            case BundleTypeEnum.SIMFREE:
                                return <SimfreeComponent key={"SIMFREE_"+idx} {...bundleProps} />;
                            case BundleTypeEnum.SIMFREE_INST:
                                return <SimfreeComponent key={"SIMFREE_INST_"+idx} {...bundleProps} />;
                            case BundleTypeEnum._1P_PRE:
                                return  <FixComponent key={"FIX_1P_"+idx} {...bundleProps} />;
                            case BundleTypeEnum._1P_INF:
                                return  <FixComponent key={"FIX_1P_"+idx} {...bundleProps} />;
                            case BundleTypeEnum._3P_PRE:
                                return  <FixComponent key={"FIX_3P_"+idx} {...bundleProps} selectedNumberVoip={this.props.selectedVoipNumber} />;
                            case BundleTypeEnum._3P_INF:
                                return  <FixComponent key={"FIX_3P_"+idx} {...bundleProps} selectedNumberVoip={this.props.selectedVoipNumber} />;
                            case BundleTypeEnum.HARDBUNDLE:
                                return  <ConvergentComponent key={"HARDBUNDLE_"+idx} {...bundleProps} />;
                            case BundleTypeEnum.VIDEO:
                                return <TvUpsellComponent key={"VIDEO_"+idx} {...bundleProps} />;
                            case BundleTypeEnum.MNP_APPLICATION:
                                return <MobileComponent key={"MNP_APPLICATION_"+idx} {...bundleProps} />;
                            case BundleTypeEnum.TRANSFER:
                                return  <TransferComponent key={"TRANSFER_"+idx} {...bundleProps} />;
                            default:
                                return null;
                            }
                        })
                    }
                </div>
                }
                <CartErrorModal descriptions={this.props.descriptions} errorCode={this.props.miniCart ? this.props.miniCart.errorCode : null}/>
                <MsisdnSimCardPickerModal channel={this.props.channels.sales}/>
                <OraVasPacketPickerComponent />
                {lowerInstallmentsActive && <LowerInstallmentsModal channel={this.props.channels.sales} showNetPrices={this.props.showNetPrices} descriptions={this.props.descriptions} cartIsNet={this.props.cartIsNet}/>}
            </div>
        );

    }
}

const mapDispatchToProps = dispatch => ({
    ...bindActionCreators({
        fetchMiniCart,

    }, dispatch),
    subscribeCustomerSelected: () => dispatch(subscribeCustomerSelected()),
    removeVoucher: voucher => dispatch(removeVoucher(null, null, null, voucher)),
});

const mapStateToProps = state => ({
    entries: getCartEntries(state),
    miniCart: getMiniCart(state),
    selectedVoipNumber: getSelectedVoipNumber(state),
    showNetPrices: getPriceIsNet(state),
    cartIsNet: getCartIsNet(state),
});

export default connect(mapStateToProps, mapDispatchToProps)(MulticartEntriesListContainer);
